#Create{
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 4px;
    font-size: 16px;
    color: white;
    font-weight: bold;
    background:transparent;
    width: 140px;
    height: 36px;
    position: absolute;
    top: 92px;
    right: 260px;
}

#Create:focus{
    // outline: none;
}
.profile-main{
    background: #121212 ; 
    width: 100vw;
    display: flex;
    margin-top: -80px;
    color: #FFFFFF;

}
.profile-sidebar{
    min-width: 260px;
    background: #212121;
    width: 20vw;
    text-align: center;
    padding:80px 3vw ;  
    margin-right: 20px;

}

.name{
    font-size: 28px;
    line-height: 110%;
    font-weight: bold;
    margin-top: 20px;
}
.profile-sidebar ul {
    list-style: none;
    margin-left: 3vw;
    margin-top: 80px;
}
.profile-sidebar ul li {
    display: flex;
    margin-bottom: 40px;
    cursor: pointer;
}

.selected{
    color: white !important;
}
.profile-sidebar ul li p {
    font-size: 20px;
    line-height: 24px;
    color: rgba(255, 255, 255, 0.6);
    margin-top: 3px;
    margin-bottom: 0px;
    margin-left: 25px;
}
.profile-sidebar ul li p:hover {
    color: white !important;
 }

.profile-main-content{
    padding: 60px 2vw;
}
.profile-main-text{
    font-size: 36px;
    font-weight: bold;
    letter-spacing: -0.02em;
}
.drafts-div{
    
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.02), rgba(255, 255, 255, 0.02)), #212121;
    border-radius: 16px;
    width: 72vw;
    padding: 35px;
    margin-top: 30px;
    height: 389px;
    overflow-x: scroll;
    overflow-y: hidden;
    -ms-overflow-style: none;

}
/* Hide scrollbar for Chrome, Safari and Opera */
.drafts-div::-webkit-scrollbar {
    display: none;
}
#create-course{
    width: 390px;
    height: 272px;
}
.div-head{
    font-size: 28px;
    line-height: 100%;
    font-weight: bold;
    margin-bottom: 30px;
}
.start-creating{
    width: 317px;
    height: 148px;
    border: 1px dashed rgba(255, 255, 255, 0.2);
    box-sizing: border-box;
    border-radius: 8px;
    text-align: center;
    border-radius: 8px;
    text-align: center;
    padding: 16px;
    margin-top: 10px;

}
.start-creating > p {
    font-size: 12px;
    line-height: 120%;
    text-align: center;
    color: rgba(255, 255, 255, 0.3);
}
.start-creating button {
    width: 284px;
    height: 48px;
    background: #08C426;
    border-radius: 8px;
    font-size: 16px;
    line-height: 100%;
    color: #FFFFFF;
    font-weight: bold;border: none;
    

}
.course-card-container{
    display: flex;
    overflow-x: scroll;
    overflow-y: hidden;
    -ms-overflow-style: none;
}
/* Hide scrollbar for Chrome, Safari and Opera */
.course-card-container::-webkit-scrollbar {
    display: none;
}
.course-card{
    width: 196px;
    margin-right: 25px;
    overflow: hidden;    
}
.course-card img{
    width: 184px;
    height: 172px;
} 
.course-name{
    font-size: 20px;
    line-height: 110%;
    color: rgba(255, 255, 255, 0.9);
    margin-top: 16px;
}
.course-author{
    font-size: 12px;
    line-height: 100%;
    color: #A9A9A9;
}
#newcoursename{
    display: none;
    position: fixed;
    top: 100px;
    width: 100%;
    
}
@media (max-width: 1620px){
    .profile-sidebar{
        padding:80px 2vw ;  
    }
}
@media (max-width: 450px){
    #Create{
        display: none;
    }
    .profile-sidebar{
        display: none;
    }
    .drafts-div{
        width: 92vw;
        height: auto;
        padding: 25px;
        margin-top: 0px;
    }
    .course-card-container{
        display:grid;
    }
    #create-course{
        width: 92vw;
        height: 228px;
        margin-bottom: 20px;
    }
    .profile-main-content {
        padding: 30px 4vw;
    }
    .profile-main-text{
        font-size: 25px;
    }
    .div-head{
        font-size: 20px;
        margin-bottom: 20px;
    }

    .start-creating{
        width: auto;
        height: 136px;
    }
    .start-creating button{
        width: 50vw;
        height: 40px;
    }
    .course-card{
        width: 230px;
        margin: auto;
        margin-bottom: 20px !important;
    }
    .course-card img {
        width: 222px;
        height: 180px;
    }
}
